
import {
  defineComponent,
  ref,
  toRefs,
  computed,
  watch,
  onMounted,
  Ref
} from "vue";
import { useStore } from "vuex";

import EDMDialog from "./EDMDialog.vue";
import { GetEdm, PostUpdateEdm, GetCollection } from "@/typings/store";
import { formatImageUrl, loading } from "@/utils";
import { EdmParams, EdmResponse } from "@/typings/edm";
import { ElNotification } from "element-plus";
import { iCreateDialog } from "@/typings/component";
import { createEDMHtml } from "@/utils/createEDMHtml";
import { getCover } from "@/constants/edm-cover";
import { useRouter } from "vue-router";

const Component = defineComponent({
  name: "EDMDetail",
  components: {
    EDMDialog
  },
  props: {
    id: {
      type: Number,
      default: 0
    }
  },
  setup: function(props) {
    const store = useStore();
    const router = useRouter();
    const dialog: Ref<iCreateDialog | null> = ref(null);

    const { id } = toRefs(props);
    const subject = ref("");
    const brandName = ref("");
    const message = ref("");
    const messageBody = ref("");
    const collectionId = ref(-1);
    const collections = ref([]);
    const templateId = ref(1);

    const detail = computed(() => store.state.edm.detail);
    const tempCollections = computed(() => store.state.collection.detail);

    const getCollectionId = (detail: EdmResponse) => {
      if (!detail) return -1;
      return detail.chosen_collection;
    };

    const fetchCollection = async (id: number) => {
      await store.dispatch(GetCollection, id);
      collections.value = tempCollections.value.collection_fabrics;
    };

    const init = async () => {
      const loadingInstance = loading();
      try {
        await store.dispatch(GetEdm, id.value);
        templateId.value = detail.value.chosen_template;
        subject.value = detail.value.subject;
        brandName.value = detail.value.recipient_brand;
        message.value = detail.value.message;
        messageBody.value = detail.value.message_body;
        const _collectionId = getCollectionId(detail.value);
        if (_collectionId !== -1 && _collectionId !== null) {
          collectionId.value = _collectionId;
          await fetchCollection(_collectionId);
        }
      } finally {
        loadingInstance.close();
      }
    };

    onMounted(async () => await init());

    const onChooseCollectionClick = () => {
      if (!dialog.value) return;
      dialog.value.open();
    };

    const onUploadClick = () => {
      const element = document.createElement("input");
      element.type = "file";
      element.accept = "image/*";
      element.addEventListener("change", (e: Event) => {
        // @ts-ignore
        console.log(e.target.files);
      });
      element.click();
    };

    const onDialogSelect = async (id: number) => {
      collectionId.value = id;
      await fetchCollection(id);
    };

    const onCancelClick = async () => {
      await router.back();
    };

    const onSaveClick = async () => {
      const params: EdmParams = {
        subject: subject.value,
        recipient_brand: brandName.value,
        message: message.value,
        message_body: messageBody.value,
        html_message: createEDMHtml(
          templateId.value,
          collections.value,
          messageBody.value,
          brandName.value,
          message.value
        ),
        creator: 1, // TODO 確認之後是否還要帶
        chosen_collection:
          collectionId.value === -1 ? null : collectionId.value,
        chosen_template: detail.value.chosen_template
      };
      const data = await store.dispatch(PostUpdateEdm, {
        id: id.value,
        params
      });
      ElNotification({
        title: "Success",
        type: "success",
        message: `Save EDM ${data.subject} successful.`
      });
      await router.push({ name: "EDMDisplay", params: { id: id.value } });
    };

    return {
      dialog,
      subject,
      brandName,
      message,
      messageBody,
      templateId,
      collections,
      collectionId,
      detail,

      onChooseCollectionClick,
      onUploadClick,
      onDialogSelect,
      onCancelClick,
      onSaveClick,
      formatImageUrl,
      getCover
    };
  }
});

export default Component;
